



















import Vue from 'vue';

export default Vue.extend({
  props: {
    label: {
      type: String,
      default: 'label'
    },
    value: {
      type: [Number, String, Boolean]
    },
    itemText: {
      type: String,
      default: ''
    },
    itemValue: {
      type: String,
      default: ''
    },
    placeholder: {
      type: String,
      default: ''
    },
    items: {
      type: Array as () => Array<any>,
      default: () => []
    },
  }
});
