






























import Vue from 'vue'
export default Vue.extend({
  props: {
    label: {
      type: String,
      default: 'label'
    },
    placeholder: {
      type: String,
      default: ''
    },
    value: {
      type: [Number, String, Boolean],
      default: ''
    },
    inputType: {
      default: 'text',
      type: String
    },
    icon: {
      type: String,
      default: ''
    },
    on: {
      type: Object,
      default: null
    },
    error: {
      type: String,
      default: null
    },
    disabled: {
      type: Boolean,
      default: false
    }
  }
})
